const initialState = {
  usersData:[],
  DataCarts:[],
  logindata: false,
  drawer: false,
};

const reducers = (state = initialState, action) => {
  switch (action.type) {
	case 'GET_DATA_USERS':
      return {
            ...state,
            usersData:action.payload,
        }
	case 'SET_DATA_USERS':
      return {
            ...state,
            usersData : action.payload,
        }
	case 'SET_LOGIN':
      return {
            ...state,
            logindata : action.payload,
        }
	case 'SET_DRAWER':
      return {
            ...state,
            drawer : action.payload,
        }
	case 'ADD_DATA_CARTS':
      return Object.assign({}, state, {
        DataCarts: [...state.DataCarts, action.payload],
      });
	case 'DEL_DATA_CARTS':
      let newDataCarts =
        state.DataCarts.filter((PostData) => {
        return action.payload !== PostData;
      });
      return Object.assign({}, state, {
        DataCarts: newDataCarts,
      }); 
	case 'SET_DATA_CARTS':
      let updateDataCarts = state.DataCarts.filter((PostData) => {
		if(PostData.amount>0) {
			return [{...PostData, amount: action.payload }]
		}else{
			return action.payload !== PostData.amount;
		}
				
      });
      return Object.assign({}, state, {
        DataCarts: updateDataCarts,
      });  
	case 'SET_DATxA_CARTS':
		  return {
			...state,
			DataCarts: updateList(state.DataCarts, action)
		  };

	default:
      return state;
  }
};

function updateList(list, action) {
    const found = list.find((index) => list.index === action.payload.index);
    if (found) {
	   console.log('found',found)
	   console.log(action)
	   return [{...found, amount: action.payload }]
    }
    return [ ...list,{ amount: action.payload }];
}

export default reducers;